@import "../node_modules/@wordpress/components/build-style/style.css";
@import "../node_modules/@wordpress/block-editor/build-style/style.css";
@import "../node_modules/@wordpress/block-editor/build-style/content.css";
@import "../node_modules/@wordpress/block-library/build-style/style.css";
@import "../node_modules/@wordpress/block-library/build-style/theme.css";
@import "../node_modules/@wordpress/block-library/build-style/editor.css";
@import "../node_modules/@wordpress/format-library/build-style/style.css";
.block-editor-container {
  position: relative;
  border: solid 1px #e0e0e0;
  border-radius: 2px;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.block-editor-container > div {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0;
}
.block-editor-container input, .block-editor-container textarea {
  box-sizing: border-box;
}
.block-editor-container .block-editor {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex: 1;
  min-height: 0;
}
.block-editor-container .block-editor__header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  border-bottom: solid 1px #e0e0e0;
  flex-shrink: 0;
}
.block-editor-container .block-editor__header-toolbar {
  display: flex;
}
.block-editor-container .block-editor__content {
  display: flex;
  flex: 1;
  min-height: 0;
}
.block-editor-container .block-editor__footer {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  border-top: solid 1px #e0e0e0;
  background: #fff;
  flex-shrink: 0;
  min-height: 32px;
  font-size: 13px;
}
.block-editor-container .block-editor__editor {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 0;
  position: relative;
  min-height: 0;
}
.block-editor-container .block-editor__canvas-area {
  flex: 1;
  display: flex;
  flex-direction: row;
  min-height: 0;
  overflow: hidden;
}
.block-editor-container .block-editor__canvas-area > div[style*=height] {
  flex: 1;
  min-width: 0;
}
.block-editor-container .block-editor__left-sidebar {
  flex: 0 0 350px;
  border-right: 1px solid #e0e0e0;
  background: #fff;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.block-editor-container .block-editor__left-sidebar .block-editor-tabbed-sidebar {
  height: 100%;
}
.block-editor-container .block-editor__left-sidebar .block-editor-tabbed-sidebar__tab {
  border-radius: 0;
  background: transparent;
  border: none;
  box-shadow: none;
  flex: 1 0 auto;
  white-space: nowrap;
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 1.2;
  font-weight: 400;
  font-size: 13px;
  color: var(--wp-components-color-foreground, #1e1e1e);
  position: relative;
  padding-inline: 16px;
  height: 48px;
}
.block-editor-container .block-editor__left-sidebar-content {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
}
.block-editor-container .block-editor__left-sidebar-content .block-editor-inserter__menu {
  width: 100%;
  max-height: none;
  border: none;
  box-shadow: none;
}
.block-editor-container .block-editor__left-sidebar-content .block-editor-inserter__main-area {
  width: 100%;
  height: 100%;
}
.block-editor-container .block-editor__left-sidebar-content .block-editor-inserter__block-list {
  padding: 0.5rem;
}
.block-editor-container .block-editor__inserter-toggle.is-pressed {
  background: var(--wp-admin-theme-color, #007cba);
  color: #fff;
}
.block-editor-container .block-editor__list-view-panel {
  flex: 0 0 250px;
  border-right: 1px solid #e0e0e0;
  padding: 0.5rem;
  overflow-y: auto;
  background: #fff;
  max-height: 100%;
}
.block-editor-container .block-editor-iframe__container {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
}
.block-editor-container .block-editor-iframe__scale-container {
  flex: 1;
  min-height: 0;
}
.block-editor-container iframe[name=editor-canvas] {
  width: 100%;
  height: 100%;
  display: block;
}
.block-editor-container .block-editor-block-tools {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  position: relative;
}
.block-editor-container .block-editor__sidebar {
  flex: 0 0 300px;
  overflow-y: auto;
  border-left: 1px solid #e0e0e0;
}
.block-editor-container .block-editor__sidebar .components-panel {
  box-sizing: content-box;
  height: 100%;
  position: relative;
  border: none;
}
.block-editor-container .block-editor-inserter__menu {
  background-color: #FFFFFF;
}
.block-editor-container .block-editor-inserter__popover .components-popover__content {
  background-color: #FFFFFF;
  max-height: 400px !important;
}
.block-editor-container .block-editor-inserter__popover .block-editor-inserter__menu {
  margin: -12px -8px;
}
.block-editor-container iframe, .block-editor-container img {
  border: none;
}

.is-root-container {
  padding-left: 2rem;
  padding-right: 2rem;
}

